import React, {
  useState,
  ReactNode,
  FC
} from 'react';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: 'sm' | 'md' | 'lg';
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  style,
  onClick
}: ButtonProps) => {

  switch (size) {
    case 'sm': 32; break;
    case 'md': 48; break;
    case 'lg': 64; break;
  }

  const defaultStyles = {
    border: '1px solid #ccc',
    borderRadius: '15px',
    width: size,
  } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {children}
    </button>
  )
}

export default Button;
